.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #84d9bc;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.section {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .section {
    padding: 8rem 0;
  }
}

.header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.webp");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  padding: 5rem 0;
}
.header__logo {
  max-width: 20rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
  }
}
.header__content {
  padding: 10rem 0;
}
@media (min-width: 768px) {
  .header__content {
    padding: 15rem 0;
  }
}
.header__title {
  color: #FFFFFF;
  font-size: 4.5rem;
  text-align: center;
  margin-bottom: 2.5rem;
}
@media (min-width: 768px) {
  .header__title {
    font-size: 6rem;
    max-width: 50%;
    text-align: left;
  }
}

.btn-primary, .plan__btn {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.btn-primary:hover, .plan__btn:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .btn-primary, .plan__btn {
    width: auto;
    display: inline-block;
  }
}

.plan__btn {
  border: 1px solid #FFFFFF;
  margin-top: 2rem;
  display: block;
}

@media (min-width: 768px) {
  .create__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.create__title {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .create__title {
    margin-top: 0;
  }
}
.create__title::after {
  content: "";
  background-color: #84d9bc;
  display: block;
  width: calc(100% + 1rem);
  height: 1rem;
  margin: -1.4rem 0 0 0;
}

.benefits {
  background-color: #84d9bc;
  overflow-x: hidden;
}
@media (min-width: 768px) {
  .benefits__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
.benefits__content {
  grid-column: span 3;
}
.benefits__title {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .benefits__title {
    margin-top: 0;
  }
}
.benefits__title::after {
  content: "";
  background-color: #FFFFFF;
  display: block;
  width: calc(100% + 1rem);
  height: 1rem;
  margin: -1.4rem 0 0 0;
}
.benefits__image {
  grid-column: span 2;
  transition: transform 0.3s ease;
}
.benefits__image:hover {
  transform: rotate(8deg) scale(1.1);
}

.statistics__title {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .statistics__title {
    margin-top: 0;
  }
}
.statistics__title::after {
  content: "";
  background-color: #84d9bc;
  display: block;
  width: calc(100% + 1rem);
  height: 1rem;
  margin: -1.4rem 0 0 0;
}

.plans {
  background-color: #313c4e;
}
.plans__title {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .plans__title {
    margin-top: 0;
  }
}
.plans__title::after {
  content: "";
  background-color: #84d9bc;
  display: block;
  width: calc(100% + 1rem);
  height: 1rem;
  margin: -1.4rem 0 0 0;
}
.plans__description {
  color: #FFFFFF;
}

.our-plans {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .our-plans {
    flex-direction: row;
    gap: 0;
    max-width: 80rem;
    margin-inline: auto;
    margin-block-start: 10rem;
  }
}
.our-plans .plan {
  background-color: #FFFFFF;
  flex-grow: 1;
  padding: 16rem 4rem 4rem 4rem;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .our-plans .plan {
    border-radius: unset;
    transition: transform 0.3s ease, box-shadow 0.2s ease;
  }
  .our-plans .plan:hover {
    transform: scale(1.05);
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
  }
}
.our-plans .plan.start {
  background-image: url("../img/icono_inicio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.our-plans .plan.business {
  order: -1;
  z-index: 5;
  background-color: #84d9bc;
  background-image: url("../img/icono_negocio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
@media (min-width: 768px) {
  .our-plans .plan.business {
    order: 0;
    margin-block: -5rem;
    border-radius: 2rem;
  }
}
.our-plans .plan.pro {
  background-image: url("../img/icono_pro.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.our-plans .plan__price {
  font-size: 1.8rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.our-plans .plan__list li {
  text-align: center;
  margin-bottom: 2rem;
}
.our-plans .plan__list li:last-child {
  margin-bottom: 0;
}

.testimonials__title {
  font-size: 3.6rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .testimonials__title {
    margin-top: 0;
  }
}
.testimonials__title::after {
  content: "";
  background-color: #84d9bc;
  display: block;
  width: calc(100% + 1rem);
  height: 1rem;
  margin: -1.4rem 0 0 0;
}
@media (min-width: 768px) {
  .testimonials {
    align-items: center;
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
  }
  .testimonials__image {
    grid-column: span 2;
  }
  .testimonials__content {
    grid-column: span 3;
  }
}
.testimonials__content .testimonial {
  background-color: #84d9bc;
  border: 0.5rem solid #313c4e;
  margin-bottom: 4rem;
  padding: 2rem;
  border-radius: 1rem;
  position: relative;
}
@media (min-width: 768px) {
  .testimonials__content .testimonial {
    max-width: 70%;
  }
  .testimonials__content .testimonial:nth-child(2) {
    margin-left: 10%;
  }
  .testimonials__content .testimonial:nth-child(3) {
    margin-left: 20%;
  }
  .testimonials__content .testimonial:nth-child(4) {
    margin-left: 30%;
  }
}
.testimonials__content .testimonial::before {
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
}
.testimonials__content .testimonial:last-child {
  margin-bottom: 0;
}
.testimonials__content .testimonial__text {
  margin: 0;
  padding-left: 6rem;
}

.footer {
  background-color: #313c4e;
  color: #FFFFFF;
}
.footer__grid {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__logo {
  max-width: 20rem;
  margin: 0 auto 5rem auto;
}
@media (min-width: 768px) {
  .footer__logo {
    margin-bottom: 0;
  }
}
.footer__widget {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .footer__widget {
    margin-bottom: 0;
  }
}
.footer__title {
  color: #84d9bc;
}
@media (min-width: 768px) {
  .footer__title {
    margin-bottom: 3rem;
  }
}
.footer__nav a {
  display: block;
  color: #FFFFFF;
  margin-bottom: 1rem;
  text-align: center;
}
.footer__nav a:last-child {
  margin-bottom: 0;
}
.footer__nav a:hover {
  color: #84d9bc;
}
.footer__copyright {
  text-align: center;
  color: #FFFFFF;
  margin: 0;
  border-top: 2px solid #84d9bc;
  padding: 1rem;
  max-width: 120rem;
  margin: 0 auto;
  font-size: 1.2rem;
}